@media screen and (max-width: 580px){
    .container{
        max-width: 380px;
    }
}

@media screen and (max-width: 355px) {
    #desc p {
        width: 100px;
    }
}

@media screen and ( max-width: 360px ) {
    .container{
        max-width: 280px;
    }
    #text-hero h2 {
        font-size: 20px;
        font-weight: 400;
        max-width: 270px;
    }
    .skill-indicator {
        display: none;
        max-width: 350px;
    }
    .skill-box {
        max-width: 340px;
    }
    #desc p {
        width: 250px;
    }
}

@media screen and ( max-width: 750px ) {
    .skill-box  {
        width: 250px;
    }
    .progress {
        width: 130px;
    }
}

@media screen and (max-width: 990px) {
    #my-img-wall {
        margin-left: 0;
        width: 150px;
        height: 150px;
    }
    .my-img {
        width: 150px;
        height: 150px;
    }
    #desc {
        margin-top: 90px;
    }
    #desc p {
        margin-left: -100px;
        max-width: 490px;
    }
    .skill-box {
        max-width: 330px;
    }
    .progress {
        width: 305px;
    }
}

@media screen and ( max-width: 892px ) {
    #my-img-wall {
        margin-left: 0;
        width: 155px;
        height: 155px;
    }
    .my-img {
        width: 155px;
        height: 155px;
    }
    #desc {
        margin-top: 80px;
    }
    #desc p {
        width: 450px;
        margin-left: -60px;
    }
    .skill-box {
        max-width: 345px;
    }
    .progress {
        width: 305px;
    }
}

@media screen and ( max-width: 765px ) {
    #as h4 {
        display: none;
    }
    #my-img-wall {
        display: none;
    }
    #desc p {
        margin: 0;
        margin-top: -70px;
    }
    .skill-box {
        width: 255px;
    }
    .progress {
        width: 175px;
    }
}

@media screen and (max-width: 565px) {
    .skill-box {
        width: 150px;
    }
    .progress {
        width: 120px;
    }
    #desc p {
        width: 370px;
    }
    #text-hero h2 {
        font-size: 35px;
    }
}